.about-moveo {
    section {
        height: 100vh;
        padding: 5% 5%;
        background-color: rgba(255, 255, 255, 0.356);

        h3 {
            direction: rtl;
            text-align: center;
            font-weight: bold;
            font-size: 40px;
            margin-bottom: 30px;
        }

        p {
            direction: rtl;
            margin: 10px;
            font-size: 16px;
        }
    }

    .moveo-p1 {
        // background-color: white;
        background: linear-gradient(
            0deg,
            rgba(255, 255, 255, 0.356) 4%,
            rgba(255, 255, 255, 1) 49%,
            rgba(255, 255, 255, 1) 100%
        );

        article {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            pre {
                p {
                    direction: ltr;
                    margin-top: 130px;
                    text-transform: uppercase;
                    font-size: 22px;
                    font-family: cursive;
                    .dot {
                        animation: appear1 1s 1s steps(1, start) infinite;
                        display: inline-block;
                    }

                    .dot:nth-child(2) {
                        animation: appear2 1s 1s steps(1, start) infinite;
                    }

                    .dot:nth-child(3) {
                        animation: appear3 1s 1s steps(1, start) infinite;
                    }
                }
            }
        }

        img {
            max-width: 500px;
        }

        p {
            font-size: 18px;
            font-weight: bold;
        }
    }

    .p2 {
        background: linear-gradient(
            0deg,
            rgba(255, 255, 255, 1) 0%,
            rgba(255, 255, 255, 1) 50%,
            rgba(255, 255, 255, 0.356) 100%
        );
        padding-top: 0;

        .imgs {
            margin-top: 100px;
            display: flex;
            gap: 30px;
            flex-wrap: wrap;
            justify-content: space-between;
            img {
                max-width: 400px;
            }
        }

        h1 {
            font-size: 50px;
            font-weight: bold;
        }

        article {
            p {
                text-align: center;
            }
            aside {
                text-align: center;
            }
        }
    }

    .p3 {
        background-color: white;
        text-align: center;
        h1 {
            font-size: 50px;
            font-weight: bold;
            text-align: center;
            color: #b8605d;
            margin-bottom: 40px;
        }

        a {
            cursor: pointer;
            margin: 0 auto;
            text-transform: uppercase;
            color: #2834cf;
            
            &:hover{
                font-size: 20px;
            }
        }
    }
}
