.app {
    background-image: url(../../imgs/pImg.jpg);
    nav {
        position: fixed;
        width: 100vw;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.445);
        text-transform: uppercase;

        display: flex;
        align-items: center;
        justify-content: space-around;

        button {
            all: unset;
            color: white;
            cursor: pointer;
            padding: 20px 15px;
            &:hover {
                transition: 0.3s;
                color: lightblue;
            }
        }
    }
    main {
        // margin: 0% 10%;
        // color: white;
        color: rgba(0, 0, 0, 0.774);
    }

    .change-color {
        background-color: white;
        // color: white;
        nav {
            background-color: #b8605d;
            color: white;
        }
    }
}
