.about-me {
    section {
        height: 100vh;
        padding: 5% 5%;

        .computer-img {
            max-width: 500px;
        }

        h1 {
            font-weight: bold;
            font-size: 40px;
        }

        p {
            direction: rtl;
            margin: 5px;
        }

        h3 {
            direction: rtl;
        }

        article {
            padding: 40px;
            ul {
                direction: rtl;
                margin-top: 30px;
                li {
                    padding: 5px;
                    font-size: 18px;
                }
            }

            h3 {
                direction: rtl;
                text-align: center;
                font-weight: bold;
                font-size: 40px;
            }

            h4 {
                direction: rtl;
            }

            aside {
                margin-top: 25px;
            }
        }
        .list {
            aside {
                li {
                    list-style-type: none;
                }
                display: flex;
                justify-content: space-around;
            }
        }
    }

    .p1 {
        display: flex;
        justify-content: center;
        align-items: center;
        background: linear-gradient(
            0deg,
            rgba(255, 255, 255, 0.356) 0%,
            rgba(255, 255, 255, 1) 49%,
            rgba(255, 255, 255, 1) 100%
        );
    }

    .p2 {
        .txt {
            display: flex;
            justify-content: space-between;
        }

        pre {
            text-align: center;
            img {
                width: 250px;
                height: 250px;
                border-radius: 50%;
            }
        }
    }

    .p3 {
        background: linear-gradient(
            0deg,
            rgba(255, 255, 255, 1) 0%,
            rgba(255, 255, 255, 1) 69%,
            rgba(255, 255, 255, 0.356) 100%
        );

        aside {
            text-align: center;
        }
    }
}
